/* creating css loader */
#loading {
    width: 2rem;
    height: 2rem;
    border: 5px solid #f3f3f3;
    border-top: 6px solid #F99E1B;
    border-radius: 100%;
    top: 50%;
    left: 50%;
    position:absolute;
    visibility: hidden;
    animation: spin 1s infinite linear;
    z-index: 9999;
}
#loading.display {
    visibility: visible;
}
@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}