.tsl-nav-link, .tsl-nav-link:hover {
    background-color: #91908E;
    color:black;
    padding-left: 15px;   
}
.tsl-nav-link.active {
    background-color: #F99E1B;
    background-image: linear-gradient(-90deg, #FF7300, #F99E1B);
    color: white;
    padding-left: 15px;
}
.bg-gary {
    background-color: #85858D;
}
.tsl-nav-link span, .tsl-nav-link.active span{
    display: inline-block;
    width: 75px;
}
.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    left: 50%;
 }
body {
    font-family: "din-2014",Helvetica,Arial,sans-serif;
}

.btn-secondary, .btn-secondary:hover{
    background-color: #ff7e00;
    border-color: #ff7e00;
    color: #fff;
    border-radius: 0;
}
.btn-secondary.disabled, .btn-secondary:disabled {
    background-color: #91908E;
    border-color: #91908E;
    color: #000;
    border-radius: 0;
}

.f-heading{
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.4rem;
    letter-spacing: 1px;
}
.card-header, .card-footer{
    background-color: #ebebeb;
}
.f-color{
    color: #ff7e00;
}
/***********Error Message CSS Used in Login Page******************************************************/
 .invalid-feedback {
    display: none;
    width: 100%;
    margin-top: 0.25rem;
    font-size: .575em;
    color: #dc3545;
}
.container-height{
    height: 847px;
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}