a {
    display: contents;
    color: black;
}
.card-footer button.active{
    color: #8bcb8c;
    cursor: pointer;
}

.card-footer button, .card-footer span{
    cursor: pointer;
}

a.disabled-link {
    pointer-events: none;
    color: gray
}
.btn-check:focus+.btn, .btn:focus{
    box-shadow: none;
}
.card{
    border: 2px solid rgba(0,0,0,.125);
}
.card.active_item{
    border: 2px solid #F99E1B;
}
.card-body.slot-body{
    height: 175px;
}